export function Header() {
  const header = document.querySelector('.js-header')
  const headerMenuButton = document.querySelector('.js-header-menu-button')
  const headerMenuLinks = document.querySelector('.js-header-menu').querySelectorAll('a')

  header.classList.toggle('is-dark', window.scrollY > 0)

  window.addEventListener('scroll', () => {
    header.classList.toggle('is-dark', window.scrollY > 0)
  })

  function openMenu() {
    header.classList.add('is-menu-opening')
    document.body.classList.add('is-menu-opening')
  }

  function closeMenu() {
    header.classList.remove('is-menu-opening')
    document.body.classList.remove('is-menu-opening')
  }

  headerMenuButton.addEventListener('click', () => {
    if (!header) return

    const isMenuOpen = header.classList.contains('is-menu-opening')

    if (isMenuOpen) {
      closeMenu()
    } else {
      openMenu()
    }
  })

  headerMenuLinks.forEach((link) => {
    link.addEventListener('click', () => closeMenu())
  })
}