import selector from '../selectors'
import { breakpointDown } from '../constants'
const { SELECTOR_RESPONSIVE_LINK } = selector

export function onHandleResponsiveLink() {
  const responsiveLinks = document.querySelectorAll(SELECTOR_RESPONSIVE_LINK)
  const mobileMatchMedia = window.matchMedia(breakpointDown.md)
  const SUNTORY_URL = 'https://www.suntory-kenko.com'
  const SUNTORY_SP_URL = SUNTORY_URL + '/sp'

  function handleResponsiveLink() {
    responsiveLinks.forEach((link) => {
      const isMobile = mobileMatchMedia.matches
      const href = link.getAttribute('href')
      const hasSPInLink = href.includes(SUNTORY_SP_URL)

      if (isMobile) {
        if (hasSPInLink) return;

        link.href = href.replace(SUNTORY_URL, SUNTORY_SP_URL)
      } else if (hasSPInLink) {
        link.href = href.replace('/sp/', '/')
      }
    })
  }

  if (mobileMatchMedia.matches) handleResponsiveLink()

  mobileMatchMedia.addEventListener('change', handleResponsiveLink)
}
