export const handleModal = () => {
  const openBtnItems = document.querySelectorAll(SELECTOR_MODAL_OPEN)
  const closeBtn = document.querySelector(SELECTOR_MODAL_CLOSE)
  const modal = document.querySelector(SELECTOR_MODAL)
  const body = document.querySelector('body')

  if (!modal) return
  const mapModal = modal.querySelector('.js-map-append')

  const handleOpenModal = () => {
    modal.classList.add('open')
    body.style.overflow = 'hidden'
  }

  openBtnItems.forEach((btn) => {
    btn.addEventListener('click', () => {
      const key = btn.dataset.key
      const html = mapStrategies[key]
      mapModal.innerHTML = html ?? ''
      handleOpenModal()
    })
  })

  closeBtn.addEventListener('click', () => {
    modal.classList.remove('open')
    body.style.overflow = ''
  })
}
