import selectors from "../selectors";
const { SELECTOR_PAGE_INTRO } = selectors
const ANIMATION_TIME = 4000; //ms

export async function playPageIntro() {
  const pageIntro = document.querySelector(SELECTOR_PAGE_INTRO);

  if (pageIntro) {
    pageIntro.classList.add('is-animating');
    document.body.style.overflow = 'hidden';

    setTimeout(() => {
      document.body.style.removeProperty('overflow');
      pageIntro.classList.remove('is-animating');
      pageIntro.classList.add('is-animated');
      pageIntro.remove()
    }, ANIMATION_TIME);
  }
}
