import Splide from '@splidejs/splide'
import { playPageIntro } from '../components/page-intro'
import { breakPoint, breakpointDown } from '../constants'

function handleConceptProductSplide() {
  const conceptSplides = document.querySelectorAll('.js-concept-product-splide')

  conceptSplides.forEach((productSplide) => {
    new Splide(productSplide, {
      type: 'fade',
      rewind: true,
      arrows: false,
      pagination: false,
      autoplay: true,
      pauseOnHover: false
    }).mount()
  })
}

export function TopPageEvent() {
  const video = document.querySelector('#main-video')
  const videoWrapper = document.querySelector('#main-video-wrapper')
  const videoBottom = videoWrapper.offsetHeight + videoWrapper.offsetTop
  const visualSection = document.querySelector('#visual-section')
  const visualSectionBottom = visualSection.offsetHeight
  const initVideoPosition = video.getBoundingClientRect().top

  video.addEventListener('canplay', async () => {
    playPageIntro()

    setTimeout(() => {
      video.play();
      video.setAttribute('autoplay', true);
    }, 1000)
  })

  function setVideoPosition() {
    if (window.innerWidth > breakPoint.md) return

    if (window.scrollY > 0) {
      const positionTop = Math.max(0, initVideoPosition - window.scrollY)
      const positionBottom = Math.max(window.innerHeight - window.scrollY - videoBottom, 0)

      videoWrapper.style.top = positionTop + 'px';
      videoWrapper.style.bottom = positionBottom + 'px';
    } else {
      videoWrapper.style.removeProperty('top');
      videoWrapper.style.removeProperty('bottom');
    }
  }

  function setWindowBackground() {
    videoWrapper.classList.toggle('is-fade', window.scrollY > visualSectionBottom / 2)
    setVideoPosition()
  }

  window.addEventListener('scroll', () => {
    setWindowBackground()
  })

  const mobileMediaMatch = matchMedia(breakpointDown.md)

  mobileMediaMatch.addEventListener('change', (event) => {
    if (event.matches) {
      setWindowBackground()
    }
  })

  setWindowBackground()

  try {
    handleConceptProductSplide()
  } catch { }
}
