import selectors from '../selectors'
const {
  SELECTOR_ACCORDION_TOGGLE_BUTTON,
  SELECTOR_ACCORDION,
  SELECTOR_ACCORDION_BODY,
  SELECTOR_ACCORDION_INNER,
} = selectors

export const handleAccordions = () => {
  const accordionToggleButtons = document.querySelectorAll(
    SELECTOR_ACCORDION_TOGGLE_BUTTON
  )

  accordionToggleButtons.forEach((button) => {
    button.onclick = () => {
      const accordion = button.closest(SELECTOR_ACCORDION)
      const accordionBody = accordion.querySelector(SELECTOR_ACCORDION_BODY)
      if (!accordion || !accordionBody) return

      const isShow = accordion.classList.contains('is-show')
      const accordionInner = accordion.querySelector(SELECTOR_ACCORDION_INNER)
      const heightElement = accordionInner.clientHeight

      const onToggle = () => {
        accordionBody.removeAttribute('style')
        accordionBody.removeEventListener('transitionend', onToggle)
      }
      accordion.classList.toggle('is-show', !isShow)
      accordionBody.style.height = `${heightElement}px`

      if (isShow) {
        setTimeout(() => {
          accordionBody.style.height = 0 + 'px'
        }, 10)
      }

      accordionBody.addEventListener('transitionend', onToggle)
    }
  })
}
