import { onHandleResponsiveLink } from './components/responsive-link'
import { Header } from './components/header'
import { TopPageEvent } from './pages/top'
import { TranslateOnScroll } from './components/translate-scroll'
import { handleAccordions } from './components/accordion'
import { handleModal } from './components/modal'
import { handleSlider } from './components/slider'
import { handleScrollToElement } from './components/anchor-link'
import { FadeTop, FadeTopSP } from './fade-top'
import { handleHiddenLoading } from './loading'

document.addEventListener('DOMContentLoaded', function () {
  onHandleResponsiveLink(),
  handleAccordions(),
  handleModal(),
  handleSlider(),
  handleScrollToElement(),
  FadeTop()
  FadeTopSP()
  Header()
  TranslateOnScroll()
  TopPageEvent()
})

window.addEventListener('load', () =>{
  handleHiddenLoading()
})
