import Splide from '@splidejs/splide';

const SLIDER_OPTIONS = {
  type: 'fade',
  arrows: false,
  pagination: false,
  drag: false,
};

const initializeSlider = (slider, thumbnails) => {
  const handleToggle = (index) => {
    thumbnails.forEach((item, idx) => item.classList.toggle('active', idx === index));
  };

  thumbnails.forEach((thumb, index) => {
    thumb.addEventListener('click', () => {
      slider.go(index);
      handleToggle(index);
    });
  });

  handleToggle(0);
};

export const handleSlider = () => {
  const sliderItems = document.querySelectorAll('.js-slider-thumbnail');

  sliderItems.forEach((item) => {
    const splide = item.querySelector('.splide');
    const thumbnailItems = item.querySelectorAll('.js-thumbnail > div');

    const slider = new Splide(splide, SLIDER_OPTIONS).mount();

    initializeSlider(slider, thumbnailItems);
  });
};
