import { isSP } from "../utils"
import selectors from "../selectors"
const { SELECTOR_TRANSLATE_SCROLL } = selectors

const OFFSET = 500

function getTransform(direction, distance) {
  switch (direction) {
    case 'top':
      return `translateY(-${distance}px)`
    case 'bottom':
      return `translateY(${distance}px)`
    case 'left':
      return `translateX(-${distance}px)`
    case 'right':
      return `translateX(${distance}px)`
  }
}

// {
//   direction: 'top' | 'bottom' | 'left' | 'right',
//   span: number,
//   disableSP: boolean,
// }

export function TranslateOnScroll() {
  const elements = document.querySelectorAll(SELECTOR_TRANSLATE_SCROLL)

  elements.forEach((element) => {
    const data = JSON.parse(element.dataset.translateScroll || "{}")
    const disableSP = data.disableSP || false

    const direction = data.direction || 'top'

    function translateElement(element) {
      if (disableSP && isSP()) return

      const elementPos = element.parentElement.offsetTop + element.parentElement.clientHeight * 0.5
      const windowCenterPos = window.scrollY + window.innerHeight * 0.5
      const span = parseInt(data.span || elementPos)
      const ratio = parseInt(data.ratio || 1)

      const distance = Math.min(
        Math.max(((windowCenterPos - elementPos) / elementPos) * OFFSET, -span),
        span
      )

      element.style.transform = getTransform(direction, distance * ratio)
    }

    translateElement(element)
    window.addEventListener('scroll', () => translateElement(element))
  })
}